<template>
  <div class="echart-container">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/soc-flow-report' }">运营中心</el-breadcrumb-item>
        <el-breadcrumb-item>流量安全报表</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <div class="container-style" v-loading.fullscreen.lock="loadingPage">
      <el-row>
        <el-col :span="24" style="margin-bottom: 15px;">
          <div class="echart-select">
            <el-select v-model="domain" placeholder="请选择" @change="onChangeDomain" size="small"
              style="margin-right: 10px;">
              <el-option v-for="item in domainList" :key="item.domain" :label="item.domain" :value="item.domain">
              </el-option>
            </el-select>

            <el-select v-model="timeZone" placeholder="请选择" @change="onChangeTime" size="small">
              <el-option v-for="item in timeList" :key="item.key" :label="item.value" :value="item.key">
              </el-option>
            </el-select>
          </div>

        </el-col>
      </el-row>
      <el-row :gutter="15">
        <el-col :span="6">
          <el-card class="box-card" shadow="never" v-loading="loading.countTotle" style="background-color: #fc8452;">
            <template #header>
              <div class="card-header">
                <span>Web攻击次数</span>
              </div>
            </template>
            <div class="card-text">
              <span>{{ countTotle }}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="box-card" shadow="never" v-loading="loading.apiCountTotle" style="background-color: #fac858;">
            <template #header>
              <div class="card-header">
                <span>Web攻击接口数</span>
              </div>
            </template>
            <div class="card-text">
              <span>{{ apiCountTotle }}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="box-card" shadow="never" v-loading="loading.ipCountTotle" style="background-color: #91cc75;">
            <template #header>
              <div class="card-header">
                <span>Web攻击IP数</span>
              </div>
            </template>
            <div class="card-text">
              <span>{{ ipCountTotle }}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="box-card" shadow="never" v-loading="loading.isocodeCountTotle"
            style="background-color: #73c0de;">
            <template #header>
              <div class="card-header">
                <span>Web攻击来源区域数量</span>
              </div>
            </template>
            <div class="card-text">
              <span>{{ isocodeCountTotle }}</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row :gutter="15" style="margin-top: 15px; ">
        <el-col :span="12">
          <el-card class="box-card" shadow="never" style="background-color:#d2f3f7;">
            <template #header>
              <div class="card-header" style="color: rgb(91 92 110);">
                <span>Web攻击来源地区</span>
              </div>
            </template>
            <div id="attack-geoip"></div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card-empty" shadow="never" v-loading="loading.countTrend"
            style="background-color:#ffeaea;">
            <template #header>
              <div class="card-header" style="color: rgb(91 92 110);">
                <span>Web攻击趋势</span>
              </div>
            </template>
            <div v-if="countTrend.length == 0" class="empty-box">
              <el-empty description="NO DATA" />
            </div>
            <div id="count-trend"></div>
          </el-card>
        </el-col>
      </el-row>
      <el-row :gutter="15" style="margin-top: 15px; ">
        <el-col :span="12">
          <el-card class="box-card-empty" shadow="never" v-loading="loading.isocodeTop"
            style="background-color: #fdfcf6;">
            <template #header>
              <div class="card-header" style="color: rgb(91 92 110);">
                <span>Web攻击来源区域 TOP 5</span>
              </div>
            </template>
            <div v-if="isocodeTop.length == 0" class="empty-box">
              <el-empty description="NO DATA" />
            </div>
            <div id="isocode-top"></div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card-empty" shadow="never" v-loading="loading.typeTop" style="background-color: #fdfcf6;">
            <template #header>
              <div class="card-header" style="color: rgb(91 92 110);">
                <span>Web攻击防护策略 TOP 5</span>
              </div>
            </template>
            <div v-if="typeTop.length == 0" class="empty-box">
              <el-empty description="NO DATA" />
            </div>
            <div id="type-top"></div>
          </el-card>
        </el-col>
      </el-row>
      <el-row :gutter="15" style="margin-top: 15px; ">
        <el-col :span="12">
          <el-card class="box-card-empty" shadow="never" v-loading="loading.apiTop" style="background-color: #fdfcf6;">
            <template #header>
              <div class="card-header" style="color: rgb(91 92 110);">
                <span>Web攻击接口 TOP 5</span>
              </div>
            </template>
            <div v-if="apiTop.length == 0" class="empty-box">
              <el-empty description="NO DATA" />
            </div>
            <div id="api-top"></div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card-empty" shadow="never" v-loading="loading.ipTop" style="background-color: #fdfcf6;">
            <template #header>
              <div class="card-header" style="color: rgb(91 92 110);">
                <span>Web攻击IP TOP 5</span>
              </div>
            </template>
            <div v-if="ipTop.length == 0" class="empty-box">
              <el-empty description="NO DATA" />
            </div>
            <div id="ip-top"></div>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import echarts from "../assets/scripts/echart";
import world from "../assets/scripts/world.json"
import { mixin, JXAjax, nameMap } from "../assets/scripts/common";
const country = require('../assets/scripts/country.json');
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      logSource: "",
      domainList: [],
      domain: "",
      timeZone: "7day",
      timeList: [
        { key: "7day", value: "7天" },
        { key: "30day", value: "30天" },
        { key: "24hour", value: "24小时" },
        { key: "1hour", value: "1小时" },
      ],
      loading: {
        countTotle: true,
        apiCountTotle: true,
        ipCountTotle: true,
        isocodeCountTotle: true,
        countTrend: true,
        apiTop: true,
        typeTop: true,
        ipTop: true,
        isocodeTop: true,
      },
      countTotle: "0",
      apiCountTotle: "0",
      ipCountTotle: "0",
      isocodeCountTotle: "0",
      countTrend: [],
      apiTop: [],
      typeTop: [],
      ipTop: [],
      isocodeTop: [],
    };
  },
  created() {
    // if (location.href.indexOf("#reloaded") == -1) {
    //   location.href = location.href + "#reloaded";
    //   location.reload();
    // }
  },

  mounted() {
    this.getdomainListData();
    this.getDataConf();
  },
  methods: {
    getdomainListData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_domain_list",
        {},
        function (response) {
          t.loadingPage = false;
          t.domainList = response.data.message;
          // t.domainList.unshift({ domain: "All" });
          // t.domain = t.domainList[0].domain;
        },
        function () {
          t.loadingPage = false;

        },

      );
    },
    getDataConf() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_sys_log_conf",
        {},
        function (response) {
          t.loadingPage = false;
          t.logSource = response.data.message.log_conf_remote;
          if (t.logSource == "false") {
            t.$message({
              duration: 0,
              showClose: true,
              message: "报表配置未设置日志数据来源，" + "<a href='/#/sys-log-conf' class='error-message-btn'>" + "请前往配置" + "</a>",
              type: "warning",
              dangerouslyUseHTMLString: true,
            });
          } else {
            t.getData();
          }

        },
        function () {
          t.loadingPage = false;
        }
      );
    },
    getData() {
      this.gatAttackGeoip();
      this.getCountTotle();
      this.getApiCountTotle();
      this.getIsocodeCountTotle();
      this.getIpCountTotle();
      this.getCountTrend();
      this.getApiTop();
      this.getTypeTop();
      this.getIpTop();
      this.getIsocodeTop();
    },
    formatterIsoCode(res_data, type) {
      var data = [];
      var cn_all = 0;
      var flag = type || 'default';
      if (res_data && res_data.length > 0) {
        res_data.forEach(item => {
          for (var i = 0; i < country.length; i++) {
            if (item.iso_code == country[i].code) {
              // 中国 = 中国大陆 + 台湾 + 澳门 + 香港
              if (item.iso_code == 'CN' || item.iso_code == 'HK' || item.iso_code == 'MO' || item.iso_code == 'TW') {
                cn_all += item.attack_count
              } else {
                data.push({ name: country[i].cnName, value: item.attack_count });
              }
            }
            if (item.IsoCode == country[i].code) {
              // 中国 = 中国大陆 + 台湾 + 澳门 + 香港
              if (item.IsoCode == 'CN' || item.IsoCode == 'HK' || item.IsoCode == 'MO' || item.IsoCode == 'TW') {
                cn_all += item.attack_count
              } else {
                data.push({ name: country[i].cnName, attack_count: item.attack_count });
              }
            }
          }
        })

        if (cn_all != 0 && flag == "default") {
          data.push({ name: '中国', value: cn_all });
        }
        if (cn_all != 0 && flag == "attack_count") {
          data.push({ name: '中国', attack_count: cn_all });
        }
      }
      return data;
    },
    gatAttackGeoip() {
      var t = this;
      var oData = { time_zone: t.timeZone, domain: t.domain };
      var data = [];
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        "/soc/soc_flow_report_attack_geoip",
        oData,
        function (response) {
          data = t.formatterIsoCode(response.data.message);
          t.initMap("attack-geoip", data);
        },
        function () {
          t.loading.requestCountTotle = false;
        },
        "no-message"
      );
    },
    initMap(id, data) {
      var t = this;
      echarts.registerMap('world', world);
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            if (params.name) {
              return params.name + ' : ' + (isNaN(params.value) ? 0 : parseInt(params.value));
            }
          }
        },
        backgroundColor: '#d2f3f7',
        // 视觉映射组件
        visualMap: {
          type: 'continuous', // continuous 类型为连续型  piecewise 类型为分段型
          show: true, // 是否显示 visualMap-continuous 组件 如果设置为 false，不会显示，但是数据映射的功能还存在
          // 指定 visualMapContinuous 组件的允许的最小/大值。'min'/'max' 必须用户指定。
          // [visualMap.min, visualMax.max] 形成了视觉映射的『定义域』

          // 文本样式
          textStyle: {
            fontSize: 14,
            color: 'rgb(91 92 110)'
          },
          realtime: false, // 拖拽时，是否实时更新
          calculable: false, // 是否显示拖拽用的手柄
          // 定义 在选中范围中 的视觉元素
          inRange: {
            color: ['#bae7a5', 'rgb(247, 244, 148)', 'rgb(255, 178, 72)', 'rgb(252, 151, 175)'] // 图元的颜色
          }
        },
        series: [
          {
            name: 'Web攻击来源地区',
            type: 'map',
            map: 'world',
            roam: false, // 是否开启鼠标缩放和平移漫游
            zoom: 1.2,
            itemStyle: {
              areaColor: 'rgb(114, 204, 255)', // 地图区域的颜色 如果设置了visualMap，areaColor属性将不起作用
              borderWidth: 0.5, // 描边线宽 为 0 时无描边
              borderColor: '#fff', // 图形的描边颜色 支持的颜色格式同 color，不支持回调函数
              borderType: 'solid', // 描边类型，默认为实线，支持 'solid', 'dashed', 'dotted'
            },
            emphasis: {
              itemStyle: {
                areaColor: '#ff5722',
                label: { show: true }
              }
            },
            label: {
              show: false // 是否显示对应地名
            },
            data: data,
            nameMap: nameMap
          }
        ]
      };
      this.buildChart(id, option);
    },
    getCountTotle() {
      var t = this;
      t.loading.countTotle = true;
      var postUrl = "/soc/soc_flow_report_attack_count_total";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.countTotle = response.data.attack_count;
          t.loading.countTotle = false;
        },
        function () {
          t.loading.countTotle = false;
        },
        "no-message"
      );
    },
    getApiCountTotle() {
      var t = this;
      t.loading.apiCountTotle = true;
      var postUrl = "/soc/soc_flow_report_attack_api_count_total";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.apiCountTotle = response.data.attack_count;
          t.loading.apiCountTotle = false;
        },
        function () {
          t.loading.apiCountTotle = false;
        },
        "no-message"
      );
    },
    getIsocodeCountTotle() {
      var t = this;
      t.loading.isocodeCountTotle = true;
      var postUrl = "/soc/soc_flow_report_attack_isocode_count_total";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.isocodeCountTotle = response.data.attack_count;
          t.loading.isocodeCountTotle = false;
        },
        function () {
          t.loading.isocodeCountTotle = false;
        },
        "no-message"
      );
    },
    getIpCountTotle() {
      var t = this;
      t.loading.ipCountTotle = true;
      var postUrl = "/soc/soc_flow_report_attack_ip_count_total";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.ipCountTotle = response.data.attack_count;
          t.loading.ipCountTotle = false;
        },
        function () {
          t.loading.ipCountTotle = false;
        },
        "no-message"
      );
    },

    getCountTrend() {
      var t = this;
      t.loading.requestCountTrend = true;
      var postUrl = "/soc/soc_flow_report_attack_count_trend";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.countTrend = response.data.attack_trend;
          t.loading.countTrend = false;
          t.initLineChart(t.countTrend, "count-trend");
        },
        function () {
          t.loading.countTrend = false;
        },
        "no-message"
      );
    },

    getApiTop() {
      var t = this;
      t.loading.apiTop = true;
      var postUrl = "/soc/soc_flow_report_attack_api_top";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loading.apiTop = false;
          t.apiTop = response.data.result;
          t.initBarChart(t.apiTop, "api-top", "api");
        },
        function () {
          t.loading.apiTop = false;
        },
        "no-message"
      );
    },
    getTypeTop() {
      var t = this;
      t.loading.typeTop = true;
      var postUrl = "/soc/soc_flow_report_attack_type_top";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loading.typeTop = false;
          t.typeTop = response.data.result;
          t.initBarChart(t.typeTop, "type-top", "WafPolicy");
        },
        function () {
          t.loading.typeTop = false;
        },
        "no-message"
      );
    },
    getIpTop() {
      var t = this;
      t.loading.ipTop = true;
      var postUrl = "/soc/soc_flow_report_attack_ip_top";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loading.ipTop = false;
          t.ipTop = response.data.result;
          t.initBarChart(t.ipTop, "ip-top", "SrcIP");
        },
        function () {
          t.loading.ipTop = false;
        },
        "no-message"
      );
    },
    getIsocodeTop() {
      var t = this;
      t.loading.isocodeTop = true;
      var postUrl = "/soc/soc_flow_report_attack_isocode_top";
      var oData = { time_zone: t.timeZone, domain: t.domain };
      if (t.domain == "") {
        oData = { time_zone: t.timeZone };
      }
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loading.isocodeTop = false;
          t.isocodeTop = t.formatterIsoCode(response.data.result, "attack_count");
          t.initBarChart(t.isocodeTop, "isocode-top", "name");
        },
        function () {
          t.loading.isocodeTop = false;
        },
        "no-message"
      );
    },
    onChangeDomain(value) {
      var obj = {};
      obj = this.domainList.find((item) => {
        return item.domain === value;
      });
      this.domain = obj.domain;
      this.getData();
    },
    onChangeTime(value) {
      var obj = {};
      obj = this.timeList.find((item) => {
        return item.key === value;
      });
      this.timeZone = obj.key;
      this.getData();
    },
    initLineChart(data, id) {
      var x = [];
      var y = [];
      data.forEach(item => {
        y.push(item.attack_count)
        x.push(item.time_slot)
      })
      var option = {
        // title: {
        //   text: title
        // },
        // 全局调色盘。
        color: [
          '#73c0de',
        ],
        tooltip: {
          trigger: "axis",
          // formatter: "{c}",
        },

        xAxis: {
          type: "category",
          data: x,
          show: false,
          boundaryGap: false,
          axisLabel: {
            formatter: function (value) {
              if (value.length > 20) {
                return `${value.slice(0, 20)}...`;
              }
              return value;
            }
          }
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: y,
            type: "line",
            smooth: true,
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#73c0de' },
                { offset: 0.5, color: '#9fd8ef' },
                { offset: 1, color: '#ffeaea' }
              ])
            }
          },
        ],
      };
      this.buildChart(id, option);
    },
    initBarChart(data, id, key) {
      var x = [];
      var y = [];
      data.forEach(item => {
        y.push(item.attack_count)
        x.push(item[key])
      })
      var option = {
        color: ['#73c0de'],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          top: "2%",
          left: "3%",
          right: "8%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          // 不显示刻度和数字
          splitLine: { show: false },
          axisLabel: { show: false },

        },
        yAxis: {
          type: "category",
          data: x,
          inverse: true, //排序
          splitLine: { show: false },
          axisLine: { show: false },
          axisTick: { show: false },
          barGap: 50,
          axisLabel: {
            show: true,
            inside: true,
            interval: 0,
            color: '#ababab',
            verticalAlign: 'bottom',
            fontSize: 14,
            align: 'left',
            padding: [0, 0, 10, -5],
          },
        },
        series: [
          {
            data: y,
            type: "bar",
            //柱状图自动排序，排序自动让Y轴名字跟着数据动
            realtimeSort: true,
            barWidth: 10,
            barGap: 50,
            smooth: true,
            valueAnimation: true,
            showBackground: true,
            // Y轴数字显示部分
            label: {
              interval: 0,
              show: true,
              position: "right",
              valueAnimation: true,
              color: "#177ed0",
              fontSize: 12,
            },
            emphasis: {
              itemStyle: {
                borderRadius: 7,
              }

            },
            itemStyle: {

              // 颜色样式部分
              borderRadius: 7,
              color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                { offset: 0, color: '#177ed0' },
                { offset: 1, color: '#6cd1ff' }
              ])
            }
          },
        ],
      };
      this.buildChart(id, option);
    },
    buildChart(id, option) {
      if (document.querySelector("#" + id)) {
        var _option = option || [];
        var myChart = echarts.init(document.getElementById(id));
        if (_option && typeof _option === "object") {
          myChart.setOption(_option, true);
        }
      }
    },
  },
};
</script>

